html {
    font-size: 62.5%;
    min-width: 320px
}
body {
    font-size: 1.6rem;
    line-height: 1.5;
    background: #1d1d1d;
    display: block;
    min-width: 320px;
    font-family: "Open Sans", sans-serif;
    color: #fff;
    padding-left: 13rem;
    overflow-y: scroll;
    overflow-x: hidden
}
body * {
    -webkit-box-sizing: border-box;
    box-sizing: border-box
}
:focus {
    outline: 0
}
svg {
    will-change: transform
}
a {
    color: #08fdd8;
    -webkit-transition: color 0.3s;
    -o-transition: color 0.3s;
    transition: color 0.3s;
    text-decoration: none
}
a:hover {
    color: #019e86
}
img {
    max-width: 100%
}
hr {
    float: none;
    width: 100%;
    margin-top: 2.7rem;
    margin-bottom: 2.7rem
}
