.lazyload,
.lazyloading {
    opacity: 0
}
.lazyloaded {
    opacity: 1;
    -o-transition: opacity 500ms;
    transition: opacity 500ms;
    -webkit-transition: opacity 500ms;
    z-index: 0
}
.page-outer {
    min-height: 100vh;
    position: relative
}
.tags {
    color: #515152;
    position: absolute;
    bottom: 0;
    left: 4rem;
    font-style: italic;
    font-size: 1.8rem;
    font-family: 'La Belle Aurore', cursive
}
.top-tags {
    bottom: auto;
    top: 2rem
}
.top-tags:after {
    content: '<body>';
    font-family: 'La Belle Aurore', cursive;
    color: #515152;
    font-size: 1.8rem;
    position: absolute;
    margin-left: 2rem
}
.bottom-tags {
    bottom: 2rem;
    top: auto
}
.bottom-tags:before {
    content: '</body>';
    font-family: 'La Belle Aurore', cursive;
    color: #515152;
    font-size: 1.8rem;
    position: absolute;
    margin-left: 2rem
}
.secondary-col {
    color: #FD2155
}
.primary-col {
